import React, { useEffect } from "react";
import { TweenMax, Circ } from "gsap";
import {
  Wrapper,
  Logo,
  ProgressBar,
  BarWrapper,
  Bar,
  Percent,
  PoseWrapper
} from "./styled";

const Preloader = props => {
  useEffect(() => {
    if (barRef) {
      TweenMax.to(barRef, props.time, { width: "100%", ease: Circ.easeOut });
      TweenMax.to(percentRef, props.time, {
        value: "100%",
        roundProps: "value",
        ease: Circ.easeOut
      });
    }
  });

  let barRef;
  let percentRef;
  return (
    
      <Wrapper theme="black">
        <Logo />

        <ProgressBar>
          <BarWrapper>
            <Bar ref={ref => (barRef = ref)} />
          </BarWrapper>

          <Percent ref={ref => (percentRef = ref)} value="0%" />
        </ProgressBar>
      </Wrapper>
    
  );
};

export default Preloader;
