import React, { useState, useEffect } from "react";
import { Route } from "react-router-dom";
import styled, { css, createGlobalStyle } from "styled-components";
import reset from "styled-reset";
import posed, { PoseGroup } from "react-pose";

import fontRaleway from "./fonts/raleway.css";
import greyBg from "./img/grey_bg.png";

import HomePage from "./components/Home";
import Gallery from "./components/Gallery";
import Preloader from "./components/Preloader";

const GlobalFont = css`
  ${fontRaleway}

  body {
    @import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700&subset=cyrillic");
    font-family: "Roboto", sans-serif !important;
    color: #444952;
  }
`;

const GlobalButton = css`
  button {
    font-family: inherit;
    cursor: pointer;
  }
`;

const GlobalStyles = createGlobalStyle`
  ${reset};

  ${GlobalFont}
  ${GlobalButton}

  * {
    box-sizing: border-box;
  }

  #root {
    position: relative;
    z-index: 10;
    overflow: hidden;
  }

  #fullpage {
    z-index: 10;
  }
`;

function onLoad() {
  var now = new Date().getTime();
  var page_load_time = now - performance.timing.navigationStart;
  console.log("User-perceived page loading time: " + page_load_time);
}

const calculateLoadTime = () => {
  const perfData = window.performance.timing;
  const EstimatedTime = -(perfData.loadEventEnd - perfData.navigationStart);
  var pageLoadTime = -(perfData.loadEventEnd - perfData.navigationStart);

  console.log(`PageLoad: ${pageLoadTime}`);
  const time = parseInt((EstimatedTime / 1000) % 60) / 10;

  console.log(`Time: ${time}`);

  return time;
};

const PoseWrapper = styled(
  posed.div({
    enter: { opacity: 1 },
    exit: { opacity: 0, transition: { duration: 600 } }
  })
)`
  position: fixed;
  z-index: 1000;
`;

const App = () => {
  const [loading, setLoading] = useState(true);

  const loadTime = calculateLoadTime();

  useEffect(() => {
    if (loading) {
      setTimeout(() => setLoading(false), loadTime * 1000);
    }
  });

  return (
    <React.Fragment>
      <GlobalStyles />

      <PoseGroup>
        {loading && (
          <PoseWrapper key="preloader">
            <Preloader time={loadTime} />
          </PoseWrapper>
        )}
      </PoseGroup>

      {/* {!loading && <HomePage />} */}
      <HomePage />
      {/* <Route exact path="/" component={HomePage} />
      <Route path="/gallery" component={Gallery} /> */}
    </React.Fragment>
  );
};

export default App;
